<template>
  <section>
    <article
      v-for="(item, article) in articles"
      :key="article"
      @click="shareData(article)"
    >
      <img :src="item.screen" :title="item.title" :alt="item.title" />
      <div>
        <h2>{{ item.title }}</h2>
        <p>{{ item.stack }}</p>
      </div>
    </article>
  </section>
</template>
<script>
export default {
  name: "Lab",
  data() {
    return {
      articles: [
        {
          title: "Breaking Bad Tributo",
          stack: 'Glide.js / CSS-Grid / Sass',
          description: `Página motivacional a testar o Glide.js, um plugin Javascript super leve (23kb), sem dependências 
          para criação de slider de imagens e com suporte ao mobile.`,
          url: "https://renato7oliveira.github.io/portfolio/Breaking_Bad_Tributo/src/",
          screen: require("../assets/images/portfolio-breaking-bad-tributo.jpg"),
        },
        {
          title: "Scroll Behavior simplificado",
          stack: 'HTML4 / CSS2',
          description: `2 linhas é o suficiente para este efeito interessante.`,
          url: "https://renato7oliveira.github.io/portfolio/Scroll_Smooth/src/",
          screen: require("../assets/images/portfolio-scroll-behavior.jpg"),
        },
        {
          title: "Beatles Tributo",
          stack: 'CSS3 Animate / Vue.js Component',
          description: `
          A ideia foi colocar em prática animações com CSS3 puro. Então no header há um PNG transparente
          sobreposto à uma animação e logo abaixo foi criado uma grid com CSS-Grid ocupando espaços fracionados.
          `,
          url: "https://renato7oliveira.github.io/portfolio/Beatles_Tributo/src/",
          screen: require("../assets/images/portfolio-beatles.jpg"),
        },
        {
          title: "Street Fighter Tributo (old school)",
          stack: 'Adobe Edge',
          description: `
          Projeto interativo feito com Adobe Edge (R.I.P). 
          Após o usuário escolher um personagem, sua música tema é tocada 
          e aparecem botões que emite som dos personagens. A introdução tem um 
          vídeo de abertura, então o projeto ficou pesado, mas vale conferir. As imagens 
          foram retiradas do Pinterest, fiz a diagramação e mínimos recortes.
          `,
          url: "https://renato7oliveira.github.io/street-fighter-audio-project",
          screen: require("../assets/images/portfolio-street-fighter.jpg"),
        },
      ],
    };
  },
  methods: {
    shareData(keyDetail) {
      this.$router.push({
        name: "PortfolioDetalhe",
        params: { data: this.articles[keyDetail] },
      });
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
  },
};
</script>


<style lang="scss" scoped>
section {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
  article {
    transition: 0.3s;
    &:hover {
      cursor: pointer;
      transform: translateY(-5px);
    }
    &:active {
      transform: translateY(0px);
    }
    div{
      padding: 10px 15px;
    h2 {
      font-size: 0.8em;
      font-weight: bold;
    }
    p{
      font-size: 0.8em;
      color: #999;
    }
    }
    img {
      width: 100%;
      border-radius: 15px 15px 0 0;
      border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    }
  }
}
/* MEDIA QUERIES START */
@media (max-width: 800px) {
  section {
    grid-template-columns: 1fr;
    margin-bottom: 90px;
  }
}
/* MEDIA QUERIES END  */
</style>